import React from 'react'
import Logo from '../Mask Group 69.svg'
import '../App.css';
import Search from '../Component 3 – 1.svg'
import Img from '../3.svg';
import Step2 from '../step-2.js'
import Step3 from '../step3.js'
import {ReactComponent as Path13} from '../Path 13.svg'
import  {ReactComponent as Path14} from '../Path 14.svg'
import Next from '../Rectangle 675.svg'
import Prev from '../Rectangle 688.svg'
import Step1 from '../step1.js'

class FormUserCategory extends React.Component{
    
    
    continue=(e)=>{
        
          
     e.preventDefault()
     
   
        this.props.nextStep()
       
        
    }
    render(){
            const {values, handleChange, changeStyle}=this.props
            
        return(
            
          
    <div className="app">
        <div className="img">
           
            <h2 id="search-txt">إبحث عن
            <span >وظيفة أحلامك</span> </h2>
            <p id="register-txt">سجل الأن .. وإبدا البحث</p>
        </div>
      
        <div className="content">
            <div className="logo">
            <img src={Logo}/>
            
           </div>
            <div className="center">
            
            <div className="regTxt-container">
               <p className="reg-text" style={{marginBottom:'15px'}}> تسجيل طالب العمل</p>
                <p className="type-txt">لوريم إيبسوم هو ببساطة نص شكلي بمعنى أن الغاية هي الشكل </p>
            
            </div>
            <div className="choose">
      
                 
            <label className="labl">
            <input 
                        type="radio" 
                        name="type"
                        value="employee"
                        checked={values.levelOfEducation === "employee"}
                        onChange={handleChange}
                        style={{visibility:'hidden'}}
                    
                    />
            
            <div className="employer" id="employee"  onClick={changeStyle} onChange={handleChange} name="type" value="employee">
                   
             <Path13 />

                            <p onClick={handleChange} id="employee-text">طالب عمل </p>
           
                    </div>
            </label>
                    
                        
            <label className="labl">
                <input 
                        type="radio" 
                        name="type"
                        value="institution"
                        checked={values.levelOfEducation === "institution"}
                        onChange={handleChange}
                        style={{visibility:'hidden'}}
                    />
            
                <div className="institution" id="instiution" onClick={handleChange} name="type" value="employee">
            
          
<Path14 />
                        
                            <p onClick={handleChange} id="employee-text">مؤسسات </p>
           
                    </div>
            </label>      
               
      
      
                </div>
      
     
      
           
      
           
            
            
            </div>
            
             <div className="footer">
              <div className="progress">
                    <div className="progress-bar" role="progressbar" aria-valuenow="50" aria-valuemin="0" aria-valuemax="100" style={{width:"40%"}}>
                      <span className="sr-only">50% Complete</span>
                    </div>
                  </div>
                
            
            <div className="buttons">
      
                  <div className="prev">
                  <p id="prev-txt">السابق </p>
                  </div>
               

                   <p style={{margin:0,fontSize:22}}>   الخطوة  <span style={{fontWeight: 'bold',fontSize: 27,color:'#3A39FF', display:'inline'}}>2</span>  من<span style={{fontWeight: 'bold',fontSize: 27,color:'#3A39FF',display:'inline'}}>5</span> </p>

                  
                  <div className="next" onClick={this.continue}>

                  <p  id="next-txt">التالي </p>
                  </div>

               

          </div>
            </div>
            
            </div>
            </div>
        )
    }
    
}

export default FormUserCategory