import React from 'react'
import Logo from '../Mask Group 69.svg'
import '../App.css';
import Nav from '../nav.js'
import Search from '../Component 3 – 1.svg'
import Img from '../3.svg';
import Step2 from '../step-2.js'
import Step3 from '../step3.js'
import Path13 from '../Path 13.svg'
import Path14 from '../Path 14.svg'
import Next from '../Rectangle 675.svg'
import Prev from '../Rectangle 688.svg'
import Step1 from '../step1.js'

const styles={
    width:'100%', border: '1px solid #707070', borderRadius:'30px', fontSize:'15px', paddingRight: '2%',
    paddingTop: '1%',textAlign: 'right', height:'43px'
    
}

class Final extends React.Component{
    
    
    continue=(e)=>{
        
          
     e.preventDefault()
     
   
        this.props.nextStep('final')
       
        
    }
    
      
    prev=(e)=>{
        
          
     e.preventDefault()
     
   
        this.props.prevStep()
       
        
    }
    render(){
            const {values, handleChange}=this.props
            
            
            
        return(
            
          
    <div className="app">
        <div className="img">
           
            <h2 id="search-txt">إبحث عن
            <span >وظيفة أحلامك</span> </h2>
            <p id="register-txt">سجل الأن .. وإبدا البحث</p>
        </div>
      
        <div className="content">
            <div className="logo">
            <img src={Logo}/>
            
           </div>
            <div className="center">
               <p className="reg-text"> تسجيل طالب العمل</p>
            
            
            <div className="choose-needs">
      
                 
          
            
           
                
            
            
          <form className="user-form" style={{width:'100%'}}>
            
             <label  style={{width:"100%" ,marginBottom:'20px'}}>
            
             <input 
                    type="mail" 
                    value={values.mail} 
                    name="mail" 
                    placeholder="الإيميل" 
                    onChange={handleChange} 
            
            style={styles}
                />
            
            
          
             <span style={{fontSize:10, color:"red"}}>{values.mailError} </span>
            
            
              </label>
            
            <label id="password" style={{width:"100%"}}>
             <input 
                    type="password" 
                    value={values.password} 
                    name="password" 
                    placeholder="الرقم السري" 
                    onChange={handleChange} 
                    style={styles}
                />
            <span style={{fontSize:10, color:"red"}}>{values.passwordError} </span>
            <input 
                    type="password" 
                    value={values.rePassword} 
                    name="rePassword" 
                    placeholder="إعادة الرقم السري" 
                    onChange={handleChange}
                    style={styles}
                />
            
             <span style={{fontSize:10, color:"red"}}>{values.matchingError} </span>
               
           </label>
                      </form>
                   
      
      
                </div>
     
      
             </div>
      
            <div className="footer">
              <div className="progress">
                    <div className="progress-bar" role="progressbar" aria-valuenow="50" aria-valuemin="0" aria-valuemax="100" style={{width:"100%"}}>
                      <span className="sr-only">50% Complete</span>
                    </div>
                  </div>
                
            
            <div className="buttons">
      
                  <div className="prev" onClick={this.prev}>
                  <p id="prev-txt">السابق </p>
                  </div>
               

                   <p style={{margin:0,fontSize:22}}>   الخطوة  <span style={{fontWeight: 'bold',fontSize: 27,color:'#3A39FF', display:'inline'}}>5</span>  من<span style={{fontWeight: 'bold',fontSize: 27,color:'#3A39FF',display:'inline'}}>5</span> </p>

                  
                  <div className="next" onClick={this.continue}>

                  <p  id="next-txt">التالي </p>
                  </div>

               

          </div>
            </div>
            
            </div>
            </div>
        )
    }
    
}

export default Final