import React from 'react';
import FormUserCategory from './FormUserCategory.js'
import FormUserDetails from './FormUserDetails.js'
import FormUserData from './FormUserData.js'
import Final from './Final.js'
import Success from './Success.js'

class UserForm extends React.Component{
    
    state={
        step:1,
        type:'',
        typeOfNeeds:'',
        levelOfEducation:'',
        firstName:'',
        lastName:'',
        id:'',
        mobileNo:'',
        gender:'',
        birthDate:'',
        nationality:'',
        socialStatus:'',
        mail:'',
        password:'',
        rePassword:'',
        firstNameError:'',
        lastNameError:'',
        mailError:'',
        passwordError:'',
        idError:'',
        mobileNoError:'',
        fillAllError:'',
        style:{
            color:''
        }
            
        
        
    }
    
    prevStep=()=>{
        const {step}=this.state;
        
        this.setState({step:step-1})
        
    }
    
    nextStep=(event)=>{
       
        
       if(event==="final"||event==="userData"){
       
            
             const isValid=this.validate(event);
           
            if(isValid){
                
                const {step}=this.state;
               this.setState({step:step+1})
            }
        }
        
        else{
            
            if(this.state.type) {
              
                const {step}=this.state;
               this.setState({step:step+1})
           }
       
        }
           
       
       
    }
    
    validate=(event)=>{
        
        if(event==='userData'){
            
            let firstNameError=""
            let lastNameError=""
            let idError=""
            let mobileNoError=""
            let fillAllError=""
            
            
            if(this.state.firstName<2){
            firstNameError='invalid name'
                }
            
            if(this.state.lastName.length<2){
            lastNameError='invalid name'
                }
            
            if(this.state.id.length!==14){
                idError="ID number must be equal 14"
            }
            
            if(this.state.mobileNo.length!==11){
                mobileNoError="Mobile number must be equal 11"
            }
            
            if(!this.state.nationality||!this.state.socialStatus){
                fillAllError="Fill all the fields"
            }
            
            if(firstNameError||lastNameError||idError||mobileNoError||fillAllError){
            this.setState({ firstNameError, lastNameError, idError, mobileNoError,fillAllError})
            return false
        }
            
            
        }
        
        
        else{
            
             let mailError=""
             let passwordError=""
             let matchingError=""
        
        if(!this.state.mail.includes('a')||!this.state.mail.includes('.')||!this.state.mail.includes('com')){
            mailError='invalid email'
        }
          if(this.state.password<6){
            
            passwordError="Password must be larger than 6 digits"
        }
        
        if(this.state.password!=this.state.rePassword){
            matchingError="Password are not matching"
        }
        
            
      
            if(mailError||passwordError||matchingError){
            this.setState({mailError,passwordError,matchingError})
            return false
        }
            
        }
       
       
        this.setState({firstNameError:"", lastNameError:"", mailError:"",mobileNoError:'',idError:'',fillAllError:'', mailError:'',passwordError:'',matchingError:''})
        return true
        
    }
    
    handleChange=(event)=>{
        
        const {name, value, type, checked}= event.target
         
        if(event.target.id==='employee'||event.target.id==='employee-text'||event.target.id==='employee-img'){
            
            this.setState({type:'employee'})
            
            
            
        }
        else{
           type === "checkbox" ? this.setState({ [name]: checked }) : this.setState({
            [name]:value
        })
        }
        
        
    }
    
    
    changeStyle=(event)=>{
        
        console.log(event.target)
        
    }
    
    render(){

        const{step}=this.state;
        
        const{type,levelOfEducation, firstName, lastName,id, mobileNo, gender, birthDate, nationality, socialStatus, mail, password, rePassword, typeOfNeeds, mailError, firstNameError, lastNameError,idError, mobileNoError,fillAllError, passwordError, matchingError }= this.state
        
        
        const values={type,levelOfEducation, firstName, lastName,id, mobileNo, gender, birthDate, nationality, socialStatus,rePassword, typeOfNeeds, mailError,firstNameError, lastNameError, idError, mobileNoError,fillAllError, passwordError,matchingError,mail}
        
        switch(step){
                
            case 1: 
                return (
                    <FormUserCategory
                    nextStep={this.nextStep} handleChange={this.handleChange}
                    values={values} changeStyle={this.changeStyle}
                    />
                )
            case 2:
                 return(
                    <FormUserDetails 
                      nextStep={this.nextStep} handleChange={this.handleChange} prevStep={this.prevStep}
                    values={values}
                     />
                )
                
            case 3: 
                return(
                   <FormUserData 
                      nextStep={this.nextStep} handleChange={this.handleChange} prevStep={this.prevStep}
                    values={values}
                     />
                )
                
             case 4: 
                return(
                   <Final 
                      nextStep={this.nextStep} handleChange={this.handleChange} prevStep={this.prevStep}
                    values={values}
                     />
                )
                
                case 5:
                return(
                <Success values={values} />
                )
                    
        }
        
    }
}

export default UserForm