import React from 'react'
import Logo from '../Mask Group 69.svg'
import '../App.css';
import Nav from '../nav.js'
import Search from '../Component 3 – 1.svg'
import Img from '../3.svg';
import {ReactComponent as Mental} from '../Group 2082.svg'
import {ReactComponent as Vision} from '../Group 2080.svg'
import {ReactComponent as Movement} from '../Group 2084.svg'
import {ReactComponent as Hearing} from '../Group 2081.svg'
import Path13 from '../Path 13.svg'
import Path14 from '../Path 14.svg'
import Next from '../Rectangle 675.svg'
import Prev from '../Rectangle 688.svg'
import Step1 from '../step1.js'


const styles={
    width:'49%', border: '1px solid #707070', borderRadius:'30px', fontSize:'15px', paddingRight: '2%',
    paddingTop: '1%',textAlign: 'right', height:'43px'
    
}
class FormUserDetails extends React.Component{
    
    
    continue=(e)=>{
        
          
     e.preventDefault()
  
     console.log(this.props.values.typeOfNeeds)
   if(this.props.values.typeOfNeeds&&this.props.values.levelOfEducation){
       
         this.props.nextStep()
   }
      
       
        
    }
    
      
    prev=(e)=>{
        
          console.log('hiiiiiii')
     e.preventDefault()
     
   
        this.props.prevStep()
       
        
}
    
    
    render(){
            const {values, handleChange}=this.props
            
            
            
        return(
            
          
    <div className="app">
        <div className="img">
           
            <h2 id="search-txt">إبحث عن
            <span >وظيفة أحلامك</span> </h2>
            <p id="register-txt">سجل الأن .. وإبدا البحث</p>
        </div>
      
        <div className="content">
            <div className="logo">
            <img src={Logo}/>
            
           </div>
            <div className="center">
               <p className="reg-text"> تسجيل طالب العمل</p>
            
            <div className="choose-needs">
      
                 
           <p className="needs-type">نوع الاعاقة؟</p>
            
            
                <div className="choose-type">
            
       
            
           <label className="labl">
           
            
            <div className="employer" id="employee" onClick={handleChange} name="type" value="employee">
            
             <input 
                        type="radio" 
                        name="typeOfNeeds"
                        value="mental"
                        checked={values.levelOfEducation === "mental"}
                        onChange={handleChange}
                    />
                        
                        <Mental />
                            <p onClick={handleChange} id="employee-text">عقلي </p>
           
                    </div>
            </label>
            
            
            <label className="labl">
                
            
            <div className="employer" id="employee" onClick={handleChange} name="type" value="employee">
                        <input 
                        type="radio" 
                        name="typeOfNeeds"
                        value="vision"
                        checked={values.levelOfEducation === "vision"}
                        onChange={handleChange}
                    />
                        <Vision />
                            <p onClick={handleChange} id="employee-text">بصري </p>
           
                    </div>
            </label>
            
            
               <label className="labl">
                   
            
            <div className="employer" id="employee" onClick={handleChange} name="type" value="employee">
            
             <input 
                        type="radio" 
                        name="typeOfNeeds"
                        value="movement"
                        checked={values.levelOfEducation === "movement"}
                        onChange={handleChange}
                    />
                        
                       <Movement />
                            <p onClick={handleChange} id="employee-text">حركي </p>
           
                    </div>
            </label>
            
            
               <label className="labl">
            
            
            <div className="employer" id="employee" onClick={handleChange} name="type" value="employee">
            
            <input 
                        type="radio" 
                        name="typeOfNeeds"
                        value="اhearing"
                        checked={values.levelOfEducation === "hearing"}
                        onChange={handleChange}
                    />
                        
                       <Hearing />
                            <p onClick={handleChange} id="employee-text">سمعي </p>
           
                    </div>
            </label>
            
            
            
                   
      
      
                </div>
     
      
             </div>
            
            <div className="choose-needs">
      
                 
           <p className="needs-type"> المستوي التعليمي ؟</p>
            
            
                <div className="choose-type">
            
                   <form style={{width:'93%', marginRight:'4%'}}>
            
            <label style={styles} >
                    <input 
                        type="radio" 
                        name="levelOfEducation"
                        value="noteducated"
                        checked={values.levelOfEducation === "noteducated"}
                        onChange={handleChange}
                        
                        
                    /> غير متعلم
                </label>
            
                    <br />
                    <label style={styles}>
                    <input 
                        type="radio" 
                        name="levelOfEducation"
                        value="educated"
                        checked={values.levelOfEducation === "educated"}
                        onChange={handleChange}
                    /> متعلم
                </label>
               
                
            
                    </form>
                
      
      
              
            
           
                      
                   
      
      
                </div>
     
      
             </div>
      
            
            
            
            </div>
            
            <div className="footer">
              <div className="progress">
                    <div className="progress-bar" role="progressbar" aria-valuenow="50" aria-valuemin="0" aria-valuemax="100" style={{width:"60%"}}>
                      <span className="sr-only">50% Complete</span>
                    </div>
                  </div>
                
            
            <div className="buttons">
      
                  <div className="prev" onClick={this.prev}>
                  <p id="prev-txt">السابق </p>
                  </div>
               

                   <p style={{margin:0,fontSize:22}}>   الخطوة  <span style={{fontWeight: 'bold',fontSize: 27,color:'#3A39FF', display:'inline'}}>3</span>  من<span style={{fontWeight: 'bold',fontSize: 27,color:'#3A39FF',display:'inline'}}>5</span> </p>

                  
                  <div className="next" onClick={this.continue}>

                  <p  id="next-txt">التالي </p>
                  </div>

               

          </div>
            </div>
            
            </div>
            </div>
        )
    }
    
}

export default FormUserDetails


